@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Mulish", sans-serif}.navbar{background-color:#fff !important}.navbar .navbar-brand{font-size:1.9rem !important;color:#2c3e50 !important}.navbar .menu_active{font-weight:bold;border-bottom:1px solid #565387}.navbar a{font-size:1.3rem;text-transform:capitalize}.navbar a:hover{color:#3498db !important}@media (max-width: 991px){.navbar-brand{font-size:1.5rem !important}.menu_active{border-bottom:none !important;font-weight:bold;color:cornflowerblue !important}}#header{width:100%;height:80vh;margin-top:70px}#header h1{margin:0 0 10px 0;font-size:48px;font-weight:700;color:#34495e}#header .brand-name{color:#3498db}#header h2{color:#3f464d;font-size:1.4rem}#header .btn-get-started{font-weight:500;font-size:1rem;letter-spacing:1px;display:inline-block;padding:10px 30px;border-radius:50px;transition:0.5s;color:#3498db;border:2px solid #3498db;text-decoration:none}#header .btn-get-started:hover{background:#3498db;color:#fff}#header .animated{animation:up-down 2s ease-in-out infinite alternate-reverse both}.header-img{text-align:right}@keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}@-webkit-keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}@media (max-width: 772px){#header{margin-top:3px}#header h1{font-size:28px;font-weight:600}}@media (max-width: 400px){#header{margin-top:1px}#header h1{font-size:24px;font-weight:500}#header .btn-get-started{display:block;width:60%;margin:auto}}

